<template>
  <div class="tableGeral">
    <div class="tableGeral__bar tableGeral__bar__active">
      <div class="tableGeral__bar__container">
        <div class="tableGeral__bar__title">Banner de internet</div>
        <div class="tableGeral__bar__button">
          <div class="tableGeral__bar__button__container">
            <div class="tableGeral__bar__button__switch">
              <ViewIco />
            </div>
            <div class="tableGeral__bar__button__open">Abrir todos</div>
            <div class="tableGeral__bar__button__close">Fechar todos</div>
          </div>
        </div>
      </div>
    </div>

    <paginate name="banners" :list="items" :per="20">
      <li class="tableGeral__row" v-for="(item, index) in paginated('banners')" :key="item + index">
        <div class="tableGeral__row__header">
          <div class="tableGeral__row__number">{{item.id}}</div>
          <div class="tableGeral__row__veiculo">
            <div class="tableGeral__row__veiculo__key">Veículo</div>
            <div class="tableGeral__row__veiculo__valor">{{item.veiculo}}</div>
          </div>
        </div>

        <div class="tableGeral__row__content">
          <div class="tableGeral__row__item tableGeral__row__linha">
            <div class="tableGeral__row__item__key">Linha</div>
            <div class="tableGeral__row__item__valor">{{item.linha}}</div>
          </div>

          <div class="tableGeral__row__item tableGeral__row__formato">
            <div class="tableGeral__row__item__key">Formato</div>
            <div class="tableGeral__row__item__valor">
              <span class="valor1">{{item.formatow}}</span>x
              <span class="valor2">{{item.formatoh}}</span>
            </div>
          </div>

          <div class="tableGeral__row__item tableGeral__row__extensao">
            <div class="tableGeral__row__item__key">Extensão</div>
            <div class="tableGeral__row__item__valor">{{item.extensao}}</div>
          </div>

          <div class="tableGeral__row__item tableGeral__row__tempo">
            <div class="tableGeral__row__item__key">Tempo</div>
            <div class="tableGeral__row__item__valor">{{item.tempo}}</div>
          </div>

          <div class="tableGeral__row__item tableGeral__row__peso">
            <div class="tableGeral__row__item__key">Peso</div>
            <div class="tableGeral__row__item__valor">{{item.peso}}</div>
          </div>

          <div class="tableGeral__row__item tableGeral__row__loop">
            <div class="tableGeral__row__item__key">loop</div>
            <div class="tableGeral__row__item__valor">{{item.loop}}</div>
          </div>

          <div class="tableGeral__row__button">
            <div class="tableGeral__row__button__container">
              <span class="tableGeral__row__button__open">
                <ViewIco />visualizar
              </span>
              <span class="tableGeral__row__button__close">fechar</span>
            </div>
          </div>
        </div>

        <div class="tableGeral__row__banner">
          <div class="tableGeral__row__banner__container">
            <div class="tableGeral__row__banner__refresh">
              <Refresh />recarregar
            </div>
            <div class="tableGeral__row__banner__fullscreen fullscreen">
              <span>
                <Fullscreen />tela cheia
              </span>
            </div>
            <div class="tableGeral__row__banner__content">
              <div class="content_iframe" v-if="item.extensao == 'html5'" >
                <iframe
                  :src="item.url"
                  frameborder="0"
                  :width="item.formatow + 'px'"
                  :height="item.formatoh + 'px'"
                >
                </iframe>
              </div>

              <div class="content_gif" v-else>
                <div :style="{width: item.formatow + 'px', height: item.formatoh + 'px'}">
                  <img :src="item.url">
                </div>
              </div>
            </div>
          </div>
        </div>
      </li>
    </paginate>
    <paginate-links for="banners" :hide-single-page="true"></paginate-links>
  </div>
</template>

<script>
import ViewIco from '@/assets/images/viewico.svg';
import Refresh from '@/assets/images/refresh.svg';
import Fullscreen from '@/assets/images/fullscreen.svg';

import ScriptBar from './scripts/bar';
import HandleBanners from './scripts/handleBanners';

export default {
  name: 'TableGeral',
  data() {
    return {
      paginate: ['banners'],
    };
  },
  components: {
    ViewIco,
    Refresh,
    Fullscreen,
  },
  props: {
    items: {
      default() {
        return [
          {
            id: '01',
            veiculo: 'lorem',
            linha: 'geral',
            formatow: '300',
            formatoh: '250',
            extensao: 'html5',
            tempo: '15s',
            peso: '100k',
            loop: 'sim',
            url: '',
          },
        ];
      },
    },
  },
  methods: {
    goTop() {
      const buttons = document.querySelectorAll('.number a');

      buttons.forEach((button) => {
        button.addEventListener('click', () => {
          window.scrollTo({ top: 0, behavior: 'smooth' });
        });
      });
    },
    getInfoModal() {
      const modalButtons = document.querySelectorAll('.fullscreen');
      let theRow;

      const handleClick = (e) => {
        theRow = e.currentTarget.parentElement.parentElement.parentElement;
        const id = theRow.querySelector('.tableGeral__row__number').innerText;
        const veiculo = theRow
          .querySelector('.tableGeral__row__veiculo')
          .querySelector('.tableGeral__row__veiculo__valor').innerText;
        const linha = theRow
          .querySelector('.tableGeral__row__linha')
          .querySelector('.tableGeral__row__item__valor').innerText;
        const formatow = theRow
          .querySelector('.tableGeral__row__formato')
          .querySelector('.tableGeral__row__item__valor .valor1').innerText;
        const formatoh = theRow
          .querySelector('.tableGeral__row__formato')
          .querySelector('.tableGeral__row__item__valor .valor2').innerText;
        const extensao = theRow
          .querySelector('.tableGeral__row__extensao')
          .querySelector('.tableGeral__row__item__valor').innerText;
        const tempo = theRow
          .querySelector('.tableGeral__row__tempo')
          .querySelector('.tableGeral__row__item__valor').innerText;
        const peso = theRow
          .querySelector('.tableGeral__row__peso')
          .querySelector('.tableGeral__row__item__valor').innerText;
        const loop = theRow
          .querySelector('.tableGeral__row__loop')
          .querySelector('.tableGeral__row__item__valor').innerText;
        const url = theRow
          .querySelector('.tableGeral__row__banner__content')
          .querySelector('iframe')
          .getAttribute('src');

        this.$store.commit('changeInfoModal', {
          veiculo,
          id,
          linha,
          formatow,
          formatoh,
          extensao,
          tempo,
          peso,
          loop,
          url,
        });
      };

      modalButtons.forEach((button) => {
        button.addEventListener('click', handleClick);
      });
    },
  },
  mounted() {
    ScriptBar();
    this.getInfoModal();
  },
  updated() {
    HandleBanners();
    this.goTop();
  },
};
</script>

<style lang="sass">
@import '../../assets/styles/settings/vars'
@import '../../assets/styles/settings/mixins'
@import '../../assets/styles/settings/mediaQueries'
@import './styles/main'
</style>
