<template>
  <footer class="FooterGeral">
    <div class="FooterGeral__goTop" @click="goTop">
      voltar ao topo
      <ArrowUp />
    </div>

    <a :href="zip" class="FooterGeral__download">
      download do zip
      <ArrowUp />
    </a>
  </footer>
</template>

<script>
import { TweenMax } from 'gsap';
import ArrowUp from '@/assets/images/arrowup.svg';

export default {
  name: 'FooterGeral',
  components: {
    ArrowUp,
  },
  props: {
    zip: {
      default: '#',
    },
  },
  methods: {
    hideButtonTop() {
      const buttonGoTop = document.querySelector('.FooterGeral__goTop');

      TweenMax.set(buttonGoTop, {
        y: '100%',
      });

      const handleScroll = () => {
        const theTop = document
          .querySelector('.tableGeral__row')
          .getBoundingClientRect().top;

        if (theTop > 100) {
          TweenMax.to(buttonGoTop, 0.5, {
            y: '100%',
          });
        } else {
          TweenMax.to(buttonGoTop, 0.5, {
            y: 0,
          });
        }
      };

      window.addEventListener('scroll', handleScroll);
    },
    goTop() {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    },
  },
  mounted() {
    this.hideButtonTop();
  },
};
</script>

<style lang="sass">
