const ScriptBar = () => {
  const tableGeral = document.querySelector('.tableGeral');
  const bar = tableGeral.querySelector('.tableGeral__bar');

  const fixedBar = () => {
    const tableGeralTop = tableGeral.getBoundingClientRect().top;

    if (tableGeralTop <= 100) {
      bar.classList.add('tableGeral__bar__fixed');
    } else {
      bar.classList.remove('tableGeral__bar__fixed');
    }
  };

  window.addEventListener('scroll', fixedBar);
};

export default ScriptBar;
