import { TweenMax, TimelineMax, Power2 } from 'gsap';

const handleModal = () => {
  const { body } = document;
  const theModal = document.querySelector('.theModal');
  const theModalOverlay = document.querySelector('.theModal__overlay');
  const theModalHeader = document.querySelector('.theModal__header');
  const theModalContent = document.querySelector('.theModal__content');
  const openButtons = document.querySelectorAll('.tableGeral__row__banner__fullscreen');
  const closeButton = document.querySelector('.theModal__close');

  const init = () => {
    TweenMax.set(theModal, {
      display: 'none',
    });

    TweenMax.set(theModalHeader, {
      y: '-100%',
    });

    TweenMax.set(theModalContent, {
      y: 100,
      opacity: 0,
    });

    TweenMax.set(theModalOverlay, {
      transformOrigin: '0% 0%',
      x: '50%',
      y: '50%',
      rotation: 100,
    });
  };
  init();

  const closeModal = () => {
    const tl = new TimelineMax();

    tl
      .to(theModalContent, 1, {
        y: 100,
        opacity: 0,
        ease: Power2.easeInOut,
      })
      .to(theModalHeader, 1, {
        y: '-100%',
        ease: Power2.easeInOut,
      }, '-=.5')
      .to(theModalOverlay, 1, {
        rotation: 100,
        ease: Power2.easeInOut,
      }, '-=.5')
      .to(theModal, 0, {
        display: 'none',
      });

    body.classList.remove('modalActive');
  };

  const openModal = () => {
    const tl = new TimelineMax();

    tl
      .to(theModal, 0, {
        display: 'block',
      })
      .to(theModalOverlay, 1, {
        rotation: 0,
        ease: Power2.easeInOut,
      })
      .to(theModalContent, 1, {
        y: 0,
        opacity: 1,
        ease: Power2.easeInOut,
      }, '-=.5')
      .to(theModalHeader, 1, {
        y: 0,
        ease: Power2.easeInOut,
      }, '-=1');

    body.classList.add('modalActive');
  };

  closeButton.addEventListener('click', closeModal);

  openButtons.forEach((button) => {
    button.addEventListener('click', openModal);
  });
};

export default handleModal;
