import { TweenMax } from 'gsap/all';

const handleBanners = () => {
  // const theBanners = document.querySelectorAll('.tableGeral__row__banner');
  const buttonGeral = document.querySelector('.tableGeral__bar__button');
  const allRows = document.querySelectorAll('.tableGeral__row');
  const allBanners = document.querySelectorAll('.tableGeral__row__banner');
  const buttonsBanner = document.querySelectorAll('.tableGeral__row__button');
  const tableBar = document.querySelector('.tableGeral__bar');
  const refreshButtons = document.querySelectorAll('.tableGeral__row__banner__refresh');
  let allOpened = true;

  const handleRefresh = (e) => {
    const iframe = e.currentTarget.parentElement.querySelector('iframe');

    iframe.contentWindow.location.reload();
  };

  const refreshButton = () => {
    refreshButtons.forEach((button) => {
      button.addEventListener('click', handleRefresh);
    });
  };
  refreshButton();

  const verifyOpened = () => {
    const newRows = Array.from(allRows).filter(item => item.classList.contains('tableGeral__row__active'));

    if (allRows.length === newRows.length) {
      tableBar.classList.add('tableGeral__bar__active');
      allOpened = true;
    } else {
      tableBar.classList.remove('tableGeral__bar__active');
      allOpened = false;
    }
  };

  const handleButtonBanner = (e) => {
    const theRow = e.currentTarget.parentElement.parentElement;
    const theBanner = theRow.querySelector('.tableGeral__row__banner');
    const theBanerContent = theBanner.querySelector('.tableGeral__row__banner__content');
    const theHeight = theBanerContent.getBoundingClientRect().height + 50;
    const thePosition = theRow.offsetTop - 200;

    if (theRow.classList.contains('tableGeral__row__active')) {
      TweenMax.to(theBanner, 1, {
        height: 0,
      });
      theRow.classList.remove('tableGeral__row__active');
    } else {
      TweenMax.to(theBanner, 1, {
        height: theHeight,
      });
      theRow.classList.add('tableGeral__row__active');

      window.scrollTo({
        top: thePosition,
        behavior: 'smooth',
      });
    }

    verifyOpened();
  };

  const buttonBanner = () => {
    buttonsBanner.forEach((button) => {
      button.addEventListener('click', handleButtonBanner);
    });
  };
  buttonBanner();

  const openAllBanners = () => {
    if (allOpened) {
      allBanners.forEach((banner) => {
        const theHeight = banner.firstChild.getBoundingClientRect().height;

        TweenMax.to(banner, 1, {
          height: theHeight,
        });
      });
    } else {
      TweenMax.to(allBanners, 1, {
        height: 0,
      });
    }
  };

  const init = () => {
    // verifyOpened();
    // setTimeout(() => {
    //   openAllBanners();
    // }, 100);
  };
  init();

  const activeAllRows = () => {
    if (allOpened) {
      allRows.forEach((row) => {
        row.classList.add('tableGeral__row__active');
      });
    } else {
      allRows.forEach((row) => {
        row.classList.remove('tableGeral__row__active');
      });
    }
  };

  const handleButtonGeral = () => {
    if (allOpened) {
      tableBar.classList.remove('tableGeral__bar__active');
      allOpened = false;
    } else {
      tableBar.classList.add('tableGeral__bar__active');
      allOpened = true;
    }

    activeAllRows();
    openAllBanners();
  };

  buttonGeral.addEventListener('click', handleButtonGeral);
};

export default handleBanners;
